import React, { useState } from 'react';
import { Button } from '@material-ui/core';
import ListView from './ListView';
import ProtocolInstructions from "./ProtocolInstructions.json"
import swal from 'sweetalert';
import pginfo from  "./pginfo.json"
import gnrhinfo from  "./gnrhinfo.json"
const ics = require('ics')
var FileSaver = require('file-saver');

const ParentView = props => {
    const {
        UserFlow, setUserFlow, DateToStartBreeding,
        SynchronizationProtocol, 
        GNRH, PG, BullTurnIn, GestationPeriod 
    } = props;

    //Text replace within prtocol instructions
    const leadingZeroesMinutes = (DateObject) =>{
        return (DateObject.getMinutes() < 10 ? '0' : '') + (DateObject.getMinutes());
    }
    
    //String replace for PG's
    const addPGtoSteps = (text) =>{
        if(text === undefined){return ''}
        return(text.replace("${PG}", pginfo[PG]))
    }
    
    //String replace for GNRH's
    const addGNRHtoSteps = (text) =>{    
        if(text === undefined){return ''}    
        return(text.replace("${GNRH}", gnrhinfo[GNRH]))
    }

    //String.Includes() looks for TimeEdit code in JSON protocol instructions and returns the appropriate time
    const addTimedEventstoSteps = (text, dateObject) =>{    
        if(text === undefined){return ''}   
        if(text.includes("${Time-Edit}")){
            
            let plusTimeEdit = text.indexOf(":+:");
            let timeAdjustment = undefined;
          
           if (plusTimeEdit != -1){
                let analysis = text.substring(plusTimeEdit + 3)
                timeAdjustment = dateObject.getHours() + parseInt(analysis);
                return (`${timeAdjustment}:${leadingZeroesMinutes(dateObject)}`)
            }
           
        }
        return(text)
    }

    //List of instructions for this protocol 
    //example protocol 38 has 5 set of instructions for 5 different days 
    //these sets are broken down into steps 1 ... 6ish
        //Add expected birth date to list of instructions
    let ListOfInstrucitons = [...ProtocolInstructions[SynchronizationProtocol] ,  {"OnDay" : GestationPeriod.toString(), "step1" : "Expected Birth Date" }];    

    //Add bull turn in day
    let BullTurnInDict = {"OnDay" : BullTurnIn.toString() , "step1" : "Bull Turn In Begins " }

        //Find where to place Bull Turn In Date in the list of events within protocol
        let TempIndex = 0;
        for(let i = 0; i < ListOfInstrucitons.length; i++){
            if(BullTurnIn < parseInt(ListOfInstrucitons[i].OnDay)){
                TempIndex = i ;
                break;
            }
        }
        ListOfInstrucitons.splice(TempIndex, 0, BullTurnInDict)

    //Create then download the iCalendar file
    const downloadICS = () =>{
        let listOfEvents = []

        
        ListOfInstrucitons.map(instruction =>{

            let tempDate = new Date(DateToStartBreeding)
            tempDate.setDate(DateToStartBreeding.getDate() + parseInt(instruction.OnDay))

            let description = 
                (instruction.step1 === undefined ? "" : addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step1)), tempDate )) + 
                (instruction.step2 === undefined ? "" : addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step2)), tempDate )) +
                (instruction.step3 === undefined ? "" : addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step3)), tempDate )) +
                (instruction.step4 === undefined ? "" : addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step4)), tempDate )) +
                (instruction.step5 === undefined ? "" : addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step5)), tempDate )) +
                (instruction.step6 === undefined ? "" : addTimedEventstoSteps(addGNRHtoSteps(addPGtoSteps(instruction.step6)), tempDate )) ;


            let eventDictionary = {}
            let ProtocolEventDate = new Date(DateToStartBreeding);
            ProtocolEventDate.setDate(DateToStartBreeding.getDate() + parseInt(instruction.OnDay));

            //ics.createEvents api arguements found https://www.npmjs.com/package/ics
            eventDictionary["title"] = "Estrus Event"
            eventDictionary["description"] = description;
            eventDictionary["start"] = [ProtocolEventDate.getFullYear(), ProtocolEventDate.getMonth() + 1 ,ProtocolEventDate.getDate() ,ProtocolEventDate.getHours() ,ProtocolEventDate.getMinutes()]
            eventDictionary["duration"] = {hours: 1}
            listOfEvents.push(eventDictionary)

        })

        const { error, value } = ics.createEvents(listOfEvents)
          
        if (error) {
            swal(error)
            console.log(error)
            //EXIT
            return
        }
        //create file then download
        var file = new File([value], "EstrusScheduleDownload.ics", {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(file);
    }

    return (
        <>
        <div>
            <Button 
                variant   = "contained" 
                size      = "large"
                onClick={() => {
                    setUserFlow(UserFlow -1)
                }}
            >Back
            </Button>

            <Button 
                variant   = "contained" 
                size      = "large"
                onClick={() => {
                    downloadICS();
                }}
            >Download these Events as an iCalendar file (.ics)
            </Button>
            <Button 
                variant   = "contained" 
                size      = "large"
                onClick={() => {
                    window.print()
                }    
            }>
                Print
            </Button>
        </div>

            <ListView 
                ListOfInstrucitons = {ListOfInstrucitons}
                DateToStartBreeding={DateToStartBreeding}
                SynchronizationProtocol = {SynchronizationProtocol}
                GNRH={GNRH} 
                PG={PG} 
                BullTurnIn={BullTurnIn} 
                GestationPeriod={GestationPeriod}
            />
        </>
    )

}

export default ParentView;