import React, {useState} from 'react';
import { Button } from '@material-ui/core';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Select from '@material-ui/core/Select';
import Checkbox from '@material-ui/core/Checkbox';
import SynchProtocolTitleData from "./SynchronozationProtocolTitles.json"
import swal from 'sweetalert';

const ProtocolPage = props => {
    const {
        UserFlow, setUserFlow, 
        BreedType, CowOrHeifer, SemenType,
        SystemType, SynchronizationProtocol, setSynchronizationProtocol 
    } = props;
    
    let preferredList = [];
    let LesspreferredList = [];
    let OverallList = []

    const [HideLesspreferredList, setHideLesspreferredList] = useState(false);

    //Cow Prefferd #1
    const CP1 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V39
            preferredList.push(7)
        }
        else{
            if(BreedType === 1 && SystemType === 2){
                //V48
                preferredList.push(16)
            }
            else{
                if(BreedType === 1 && SystemType === 3){
                    //V54
                    preferredList.push(22)
                }
                else{
                    if(BreedType === 1 && SemenType === 1 && SystemType === 4){
                        //V72
                        preferredList.push(40)
                    }
                    else{
                        if(BreedType === 1 && SemenType === 2 && SystemType === 4){
                            //V76
                            preferredList.push(44)
                        }
                        else{
                            if(BreedType === 2 && SemenType === 1 && SystemType === 3){
                                //V69
                                preferredList.push(37)
                            }
                        }
                    }
                }
            }
        }
    }

    //Cow Prefferd #2
    const CP2 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V46
            preferredList.push(14)
        }
        else{
            if(BreedType === 1 && SystemType === 2){
                //V51
                preferredList.push(19)
            }
            else{
                if(BreedType === 1 && SystemType === 3){
                    //V61
                    preferredList.push(29)
                }
            }
        }
    }

    //Cow Prefferd #3
    const CP3 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V66
            preferredList.push(34)
        }
        else{
            if(BreedType === 1 && SystemType === 2){
                //V65
                preferredList.push(33)
            }
        }
    }

    //Cow Less Prefferd #1
    const CLPS1 = () => {

        if(BreedType === 1 && SystemType === 1){
            //V33
            LesspreferredList.push(1)
        }
        else{
            if(BreedType === 1 &&  SystemType === 2){
                //V57
                LesspreferredList.push(25)
            }
            else{
                if(BreedType === 1 && SemenType === 1 && SystemType === 3){
                    //V42
                    LesspreferredList.push(10)
                }
                else{
                    if(BreedType === 1 && SemenType === 2 && SystemType === 1){
                        //V39
                        LesspreferredList.push(7)
                    }
                    else{
                        if(BreedType === 1 && SemenType === 2 && SystemType === 2){
                            //V48
                            LesspreferredList.push(16)
                        }
                        else{
                            if(BreedType === 1 && SemenType === 2 && SystemType === 3){
                                //V71
                                LesspreferredList.push(39)
                            }
                            else{
                                if(BreedType === 2){
                                    //AC33
                                    if(SemenType === 1 && SystemType === 1){
                                        //V39
                                        LesspreferredList.push(7)
                                    }
                                    else{
                                        if(SemenType === 1 && SystemType === 2){
                                            //V48
                                            LesspreferredList.push(16)
                                        }
                                        else{
                                            if(SemenType === 1 && SystemType === 3){
                                                //V54
                                                LesspreferredList.push(22)
                                            }
                                            else{
                                                if(SemenType === 2 && SystemType === 3){
                                                    //V69
                                                    LesspreferredList.push(37)
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    //Cow Less preferred #2
    const CLPS2 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V34
            LesspreferredList.push(2)
        }
        else{
            if(BreedType === 1 && SemenType === 1 && SystemType === 3){
                //V45
                LesspreferredList.push(13)
            }
            else{
                if(BreedType === 1 && SemenType === 2 && SystemType === 1){
                //V46
                LesspreferredList.push(14)
                }
                else{
                    if(BreedType === 1 && SemenType === 2 && SystemType === 2){
                        //Blank
                    }
                    else{
                        if(BreedType === 1 && SemenType === 2 && SystemType === 3){
                            //Blank
                        }
                        else{
                            if(BreedType === 2){
                                //AC34
                                if(SemenType === 1 && SystemType === 1){
                                    //V46
                                    LesspreferredList.push(14)
                                }
                                else{
                                    if(SemenType === 1 && SystemType === 2){
                                        //V51
                                        LesspreferredList.push(19)
                                    }
                                    else{
                                        if(SemenType === 1 && SystemType === 3){
                                            //V61
                                            LesspreferredList.push(29)
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    //Cow Less preferred #3
    const CLPS3 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V35
            LesspreferredList.push(3)
        }
        else{
            if(BreedType === 1 && SemenType === 1 && SystemType === 3){
                //V67
                LesspreferredList.push(35)
            }
            else{
                if(BreedType === 1 && SemenType === 2 && SystemType === 1){
                    //V66
                    LesspreferredList.push(34)
                }
                else{
                    if(BreedType === 2){
                        //AC35
                        if(SemenType === 1 && SystemType === 1){
                            //V66
                            LesspreferredList.push(34)
                        }
                        else{
                            if(SemenType === 1 && SystemType === 2){
                            //V65
                            LesspreferredList.push(33)
                            }
                        }
                    }
                }
            }
        }
    } 

    //Cow Less preferred #4
    const CLPS4 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V47
            LesspreferredList.push(15)
        }
        else{
            if(BreedType === 1 && SemenType === 1 && SystemType === 3){
                //V71
                LesspreferredList.push(39)
            }
        }
    }

    //Heifer preferred System #1
    const HP1 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V33
            preferredList.push(1)
        }
        else{
            if(BreedType === 1 && SystemType === 2){
                //V48
                preferredList.push(16)
            }
            else{
                if(BreedType === 1 && SemenType === 1 && SystemType === 3 ){
                    //V55
                    preferredList.push(23)
                }
                else{

                    if(BreedType === 1 && SemenType === 1 && SystemType === 4 ){
                        //V73
                        preferredList.push(41)
                    }
                    else{

                        if(BreedType === 1 && SemenType === 2 && SystemType === 4 ){
                            //76
                            preferredList.push(44)
                        }
                        else{
                            if(BreedType === 1 && SemenType === 2 && SystemType === 3 ){
                                //74
                                preferredList.push(42)
                            }
                        }
                    }
                }
            }
        }
    }

    //Heifer preferred System #2
    const HP2 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V38
            preferredList.push(6)
        }
        else {
            if(BreedType === 1 && SystemType === 2){
                //V58
                preferredList.push(26)
            }
            else{
                if(BreedType === 1 && SystemType === 3){
                    //V59
                    preferredList.push(27)
                }
                else{
                    if(BreedType === 1 && SystemType === 4){
                        //V77
                        preferredList.push(45)
                    }
                }
            }
        }
    }

    //Heifer preferred System #3
    const HP3 = () => {
        if(BreedType === 1 && SystemType === 1){
            //V47
            preferredList.push(15)
        }
        else {
            if(BreedType === 1 && SystemType === 2){
                //V63
                preferredList.push(31)
            }
            else{
                if(BreedType === 1 && SystemType === 3){
                    //V64
                    preferredList.push(32)
                }
                else{
                    if(BreedType === 1 && SystemType === 4){
                        //78
                        preferredList.push(46)
                    }
                }
            }
        }
    }
    
    //Heifer preferred System #4
    const HP4 = () => {
        if(BreedType === 1 && SystemType === 3){
            //V70
            preferredList.push(38)
        }
        else{
            if(BreedType === 1 && SystemType === 4){
                //V79
                preferredList.push(47)
            }
        }
    }
    
    //Heifer preferred System #5
    const HP5 = () => {
        if(BreedType === 1 && SemenType === 2 && SystemType === 3){
            //V75
            preferredList.push(43)
        }
    }

    //Heifer Less preferred System #1
    const HLPS1 = () => {
        if(BreedType === 1 && SemenType === 1){
            //AC46
            if(SystemType === 1){
                //V35
                LesspreferredList.push(3)
            }
            else{
                if(SystemType === 3){
                    //V60
                    LesspreferredList.push(28)
                }
            }
        }
        else{
            if(BreedType === 2){
                //AC39
                if(SystemType === 1){
                    //V33
                    LesspreferredList.push(1)
                }
                else{
                    if(SystemType === 2){
                        //V48
                        LesspreferredList.push(16)
                    }
                    else{
                        if(SystemType === 3){
                            //V55
                            LesspreferredList.push(23)
                        }
                    }
                }
            }
        }
    }

    //Heifer Less preferred System #2
    const HLPS2 = () => {
        if(BreedType === 1 && SemenType === 1){
            //AC47
            if(SystemType === 1){
                //V44
                LesspreferredList.push(12)
            }
            else{
                if(SystemType === 3){
                    //V68
                    LesspreferredList.push(36)
                }
            }
        }
        else{
            if(BreedType === 2){
                //AC40
                if(SystemType === 1){
                    //V38
                    LesspreferredList.push(6)
                }
                else{
                    if(SystemType === 2){
                        //V58
                        LesspreferredList.push(26)
                    }
                    else{
                        if(SystemType === 3){
                            //V59
                            LesspreferredList.push(27)
                        }
                    }
                }
            }
        }
    }

    //Heifer Less preferred System #3
    const HLPS3 = () => {
        if(BreedType === 1 && SemenType === 1 ){ 
            //AC48
            if(SystemType === 1){
                //V46
                LesspreferredList.push(14)
            }
            else{
                if(SystemType === 3){
                    //V75
                    LesspreferredList.push(43)
                }
            }
        }
        else{
            if(BreedType === 2){
                //AC41
                if(SystemType === 1){
                    //V47
                    LesspreferredList.push(15)
                }
                else{
                    if(SystemType === 2 ){
                        //V63
                        LesspreferredList.push(31)
                    }
                    else{
                        if(SystemType === 3) {
                            //V64
                            LesspreferredList.push(32)
                        }
                    }
                }
            }
        }
    }

    //Heifer Less preferred System #4
    const HLPS4 = () => {
        if(BreedType === 1 && SemenType === 1){
            //AC49
            if( SystemType === 1){
                //V62
                LesspreferredList.push(30)
            }
        }
        else{
            if(BreedType === 2){
                //AC42
                if(SystemType === 1){
                    //V62
                    LesspreferredList.push(30)
                }
                else{
                    if(SystemType === 3){
                        //V69
                        LesspreferredList.push(37)
                    }
                }
            }
        }
    } 

    const filterProtocols = () => {
        //If working with Cows
        if(CowOrHeifer === 1){
            //7 possible additions
            //preferred Systems
            CP1();
            CP2();
            CP3();

            //Less preferred Systems
            CLPS1();
            CLPS2();
            CLPS3();
            CLPS4();
        }
        
        //If working with Heifers
        if(CowOrHeifer === 2){
            //9 possible additions
            //preferred Systems
            HP1();
            HP2();
            HP3();
            HP4();
            HP5();

            //Less preferred Systems
            HLPS1();
            HLPS2();
            HLPS3();
            HLPS4();
        }
        OverallList = [...preferredList, ...LesspreferredList]
    }

    filterProtocols();

    const CheckDataAndSubmit = () =>
    {
        if( SynchronizationProtocol === undefined ){
            swal( "Select a Protocol to Continue",{
                icon: "error",
              });
        }
        else{
            setUserFlow(UserFlow + 1)
        }
    }

    const handleCheckboxClick = (checked) => {
        setHideLesspreferredList(checked)
    }
    return (
        
        <div>
            <br />
            <br />
            <FormControl variant='outlined' >
            <FormControlLabel 
                control={
                    <Checkbox 
                        onChange={event => {
                            handleCheckboxClick(event.target.checked)
                        }}
                        defaultChecked = {HideLesspreferredList}
                    />}
                label="Hide Less Preferred Protocols" />

            </FormControl>
            <br />
            <br />

            {/* Select Protocol */}
            <FormControl variant = "outlined" >
                <InputLabel>Select Protocol</InputLabel>
                <Select 
                    className="InputStyle"
                    value    = { SynchronizationProtocol || undefined  } 
                    onChange ={ ( event ) => {setSynchronizationProtocol(event.target.value)}} 
                >
                    { HideLesspreferredList ? preferredList.map( (jsonKey, index) => {
                        return <option key={index} value={jsonKey} >{SynchProtocolTitleData[jsonKey].SynchronizationSystemTitle}</option>      
                    }) :
                    OverallList.map( (jsonKey, index) => {
                        return <option key={index} value={jsonKey} >{SynchProtocolTitleData[jsonKey].SynchronizationSystemTitle}</option>      
                    })
                     }
                    
                    
                </Select>
            </FormControl>
            <br />
            <br />

            <Button 
                onClick = {() => {setUserFlow(UserFlow -1)}}
                variant   = "contained" 
                size      = "large"
            >
            Return
            </Button>

            <Button 
                onClick = {() => {CheckDataAndSubmit()}}
                variant   = "contained" 
                size      = "large"
            >
            Submit
            </Button>
        </div>
    );
} /* end ProtocolPage */

export default ProtocolPage;