

/**
 * Header.js
 */
import React from 'react';
import './Header.css';
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
 /**
  * The header that is always fixed to the top of the page
  */
 const NavBar = () =>
 {
     return (
         <>

         <Navbar bg="light" variant="light" >
            <Container >
            <Navbar.Brand href="/"></Navbar.Brand>
            <Nav className="me-auto">
                <Nav.Link href="/">Home</Nav.Link>
                <Nav.Link href="/reference">References</Nav.Link>
                <Nav.Link href="/help">Help</Nav.Link>
                <NavDropdown title="Socials" id="basic-nav-dropdown">
                    <NavDropdown.Item href="https://www.facebook.com/beefrepro/">Facebook</NavDropdown.Item>
                    <NavDropdown.Item href="https://twitter.com/beef_repro">Twitter</NavDropdown.Item>
                    <NavDropdown.Item href="https://www.youtube.com/channel/UCLpZWNMIXm83qfMU-kqu_uA?view_as=subscriber">YouTube</NavDropdown.Item>
                </NavDropdown>
            </Nav>
            </Container>
        </Navbar>

         </>
     );
 }
 
 export default NavBar;